import { Component, h, Element } from '@stencil/core/internal';
import tailwind from '../../output.css';
import { data } from '../../data';
@Component({
  tag: 'rf-facilities',
  shadow: true,
})
export class Facilities {
  @Element() element: HTMLElement;

  data = data.My_Result;

  properties = this.data.amenities.filter(e => {
    return e.amenity_type == 'property';
  });

  activities = this.data.amenities.filter(e => {
    return e.amenity_type == 'activity';
  });

  services = this.data.amenities.filter(e => {
    return e.amenity_type == 'service';
  });

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }

  render() {
    return (
      <div>
        <div class="text-lg font-semibold mb-4 mt-10">Facilities and services</div>
        <div class="bg-gray-200 flex flex-col p-6 rounded-xl gap-3">
          <div class="flex flex-row gap-4">
            <small>{'check in: from ' + this.data.time_constraints.check_in_from + ' until ' + this.data.time_constraints.check_in_till}</small>
            <small>{'check out: ' + this.data.time_constraints.check_out_till}</small>
          </div>
          <small>{'Public areas: ' + this.data.internet_offering.public_internet_statement}</small>
          <small>{this.data.parking_offering.title + ' At ' + '$' + this.data.parking_offering.pricing}</small>
          <small>{this.data.pets_acceptance.title}</small>
          <small>{this.data.baby_cot_offering.title}</small>
          <div class="flex flex-col md:flex-row md:justify-between gap-3">
            <div class="flex flex-col gap-1">
              <small class="font-bold">Property facilities</small>
              {this.properties.map(e => {
                return <small>{e.description}</small>;
              })}
            </div>
            <div class="flex flex-col gap-1">
              <small class="font-bold">Activities</small>
              {this.activities.map(e => {
                return <small>{e.description}</small>;
              })}
            </div>
            <div class="flex flex-col gap-1">
              <small class="font-bold">Services</small>
              {this.services.map(e => {
                return <small>{e.description}</small>;
              })}
            </div>
          </div>
          <div class="flex flex-row gap-x-2">
            <small class="font-bold">Food and beverage: </small>
            <small>{this.data.description.food_and_beverage}</small>
          </div>
          <div class="flex flex-row gap-x-2">
            <small class="font-bold">Accepted credit cards at the property:</small>
            {this.data.allowed_cards.map(e => {
              return <small>{e.name}</small>;
            })}
          </div>
          <small>{this.data.description.important_info}</small>
        </div>
      </div>
    );
  }
}
